module.exports = ({ components, stores, lib, constants }) => () => {

    const $optionsBarContainer = document.createElement('div');
    $optionsBarContainer.className = 'options-bar-container';

    const $optionsBar = document.createElement('div');
    $optionsBar.className = 'options-bar';

    $optionsBarContainer.append($optionsBar)

    const visibilityToggle = lib.visibilityToggle($optionsBar);
    stores.tags.onInsert(visibilityToggle.show);

    const { modes, shapes } = constants.options;
    const { numberOption, shapeOption, keepSortedOption, showOutlineOption } = components;

    const layout = [
        modes.map(numberOption),
        shapes.map(shapeOption),
        ['size', 'spacing'].map(numberOption),
        keepSortedOption(),
        showOutlineOption()
    ];

    const groups = layout.map(option => {
        const options = [].concat(option);
        const $group = document.createElement('div');
        $group.className = 'option-group';
        $group.append(...options);
        return $group;
    });

    $optionsBar.append(...groups);

    return $optionsBarContainer;

};
