const HIDE = 'hide';
const SHOW = 'show';

module.exports = () => (targetElement, initial = false) => {

    const set = className => {
        targetElement.classList.remove(HIDE, SHOW);
        targetElement.classList.add(className);
    };

    const show = () => set(SHOW);
    const hide = () => set(HIDE);
    const toggle = bool => bool ? show() : hide();

    toggle(initial);

    return { show, hide, toggle };

};



/*

- Toggles classes between `hide` and `show`. Actual visibility determined by CSS.
- Original design only toggled `show`. Explicit `hide` was added to improve readability of CSS.
- Used to toggle visibility of `modal` and `notice` components.

*/
