module.exports = ({ lib, stores, services, components }) => () => {

    const roles = {};

    const $roleListContainer = document.createElement('div');
    $roleListContainer.className = 'role-list-container';



    const $roles = document.createElement('div');
    $roles.className = 'role-list';


    $roles.innerHTML = '<span class="label2">Bulk edit role name and colour:</span><br />';


    $roleListContainer.append($roles)

    const visibilityToggle = lib.visibilityToggle($roles);

    const sort = () => {
        services.roles.sortRoles().forEach(role => {
            const $role = roles[role.id];
            if ($role) $roles.appendChild($roles.removeChild($role));
        });
    };

    const insert = roleId => {
        const $role = components.roleCustomiser(roleId);
        roles[roleId] = $role;
        $roles.appendChild($role);
    };

    const insertThenSort = roleId => { insert(roleId); sort() };

    stores.roles.ids().forEach(insertThenSort);
    stores.roles.onInsert(insertThenSort);
    stores.options.onChange('keepSortedBy', sort);
    stores.tagInstances.onAnyChange({ roleName: sort });
    stores.tags.onInsert(visibilityToggle.show);

    return $roleListContainer;

};
